<!-- 用户管理 -->
<template>
	<!-- <tab-page-model></tab-page-model> -->
	<el-container style="padding: 10px;background:#fff;flex:1;display:flex;flex-direction:column;">
		<el-header height="40" class="search-bar">
			<span class="name">姓名</span>
			<el-input model="search.name" class="input" size="small"></el-input>
			<span model="search.userName" class="name">用户名</span>
			<el-input class="input" size="small"></el-input>
			<el-button @click="searchClick" class="button" size="small" type="primary" icon="el-icon-search"></el-button>
		</el-header>
		<el-header height="20" class="fn-bar">
			<el-button @click="addListLi" size="small" style="float:left;" icon="el-icon-plus">添加</el-button>
			<el-button @click="removeAllListLi" size="small" style="float:left;" icon="el-icon-close">删除</el-button>
		</el-header>
		<el-main style="flex:1;padding:0;">
			<el-table size="mini" class="center" style="text-align:center;" @selection-change="changeFun" @select="select" @select-all="selectAll" ref="singleTable" highlight-current-row :data="tableData" border>
				<el-table-column @selection-change="changeFun" width="100"  type="selection"></el-table-column>
				<el-table-column  label="操作" width="200">
					<template slot-scope="scope">
						<el-button @click="handleClick(scope.row)" type="text" size="mini">编辑</el-button>
						<el-button @click="removeClick(scope.row)" type="text" size="mini">删除</el-button>
					</template>
				</el-table-column>
				<el-table-column prop="name" label="姓名"></el-table-column>
				<el-table-column prop="userName" label="用户"></el-table-column>
				<el-table-column prop="role" label="角色"></el-table-column>
				<el-table-column prop="department" width="200" label="所属部门"></el-table-column>
				<el-table-column prop="phone" width="200" label="固定电话"></el-table-column>
				<el-table-column prop="tel" width="200" label="手机"></el-table-column>
				<el-table-column prop="mailbox" width="200" label="邮箱"></el-table-column>
			</el-table>
			<div class="block">
				<el-pagination @current-change="changeCurPage" layout="prev, pager, next" :total="totalPage"></el-pagination>
			</div>
			<el-dialog :title="title" width="500px" :visible.sync="dialogTableVisible">
				<el-form :model="listLi">
					<el-form-item label="姓名" :label-width="formLabelWidth">
						<el-input size="small" v-model="listLi.name" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="用户" :label-width="formLabelWidth">
						<el-input size="small" v-model="listLi.userName" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="角色" :label-width="formLabelWidth">
						<el-input size="small" v-model="listLi.role" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="所属部门" :label-width="formLabelWidth">
						<el-input size="small" v-model="listLi.department" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="固定电话" :label-width="formLabelWidth">
						<el-input size="small" v-model="listLi.phone" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="手机" :label-width="formLabelWidth">
						<el-input size="small" v-model="listLi.tel" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="邮箱" :label-width="formLabelWidth">
						<el-input size="small" v-model="listLi.mailbox" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button size="small" @click="addCancel">取消</el-button>
					<el-button size="small" type="primary" @click="addConfirm">确定</el-button>
				</div>	
			</el-dialog>
		</el-main>
	</el-container>
</template>
<script>
import tabPageModel from '@/components/tabPageModel'
export default {
	name:'userManage',
	components: {
		tabPageModel
	},
	data(){
		return {
			search: {
				userName: '',
				name: ''
			},
			title: '',
			listLi: {
				name: '',
				userName:'',
				role: '',
				department: '',
				phone: '',
				tel: '',
				mailbox: ''
			},
			checked: true,
			totalPage: 0,
			tableData: [{
				id:'1',
				name: '王小虎',
				userName:'客户1',
				role: '客户',
				department: '深圳市东方物流',
				phone: '2625314',
				tel: '13568169626',
				mailbox: ''
			}, {
				id:'2',
				name: '王小虎',
				userName:'客户1',
				role: '客户',
				department: '深圳市东方物流',
				phone: '2625314',
				tel: '13568169626',
				mailbox: ''
			}, {
				id:'3',
				name: '王小虎',
				userName:'客户1',
				role: '客户',
				department: '深圳市东方物流',
				phone: '2625314',
				tel: '13568169626',
				mailbox: ''
			}, {
				id:'4',
				name: '王小虎',
				userName:'客户1',
				role: '客户',
				department: '深圳市东方物流',
				phone: '2625314',
				tel: '13568169626',
				mailbox: ''
			}],
			dialogTableVisible: false,
			formLabelWidth: '72px',
			tableDataId: null,
			selectYes: false
		}
	},
	created(){
		for(var i = 0;i < this.tableData.length;i++){
			this.tableData[i].checked = false;
		}
	},
	mounted() {
		var leng = this.tableData.length;
		this.totalPage = leng;
	},
	methods: {
		//点击搜索
		searchClick() {

		},
		//编辑
		handleClick(row){
			this.dialogTableVisible = true;
			this.title = '编辑数据';
			var that = this;
				this.tableDataId = row.id;//编辑的这条数据的id
				this.listLi.name = row.name; 
				this.listLi.userName = row.userName; 
				this.listLi.role = row.role; 
				this.listLi.department = row.department; 
				this.listLi.phone = row.phone; 
				this.listLi.tel = row.tel; 
				this.listLi.mailbox = row.mailbox;		
			},
		//编辑与添加取消
		addCancel() {
			this.dialogTableVisible = false;
			for(var key in this.listLi){
				this.listLi[key] = '';
			}
		},
		//编辑与添加确认
		addConfirm() {
			if(this.title == '编辑数据') {
				for(var i = 0;i < this.tableData.length;i++){
					if(this.tableDataId == this.tableData[i].id){
						for(var key in this.listLi){
							this.tableData[i][key] = this.listLi[key];
							this.listLi[key] = '';
						}
						break; 
					}
				}
			} else if(this.title == '添加数据'){
				var obj = {};
				for(var key in this.listLi){
					obj[key] = this.listLi[key];
				}
				obj.id = this.tableData.length + 1;
					this.tableData.push(obj);//需要后台返回一个id
				}
				
				this.dialogTableVisible = false;
			},
		//删除
		removeClick(row){
			this.$confirm('此操作将会删除该条数据, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning',
				center: true
			}).then(() =>{
				for(var i = 0;i < this.tableData.length;i++){
					if(row.id == this.tableData[i].id){
						this.tableData.splice(i,1);
						break; 
					}
				}
			}).catch(() =>{
				this.$message({
					type: 'info',
					message: '已取消删除'
				});
			});	
		},
		//添加一条数据
		addListLi() {
			this.title = '添加数据';
			this.dialogTableVisible = true;

		},
		//删除选中的数据
		removeAllListLi() {
			var boole = false;
			for(var i = 0; i < this.tableData.length;i++) {
				if(this.tableData[i].checked) {
					boole = true;
				}
			}
			if(!boole) {
					//证明没有一项选中
					this.$message({
						message: '请至少选中一项才能删除！',
						type: 'warning'
					});
				} else {
					this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning',
						center: true
					}).then(() => {
						for(var i = 0; i < this.tableData.length;i++) {
							if(this.tableData[i].checked) {
								this.tableData.splice(i,1);
								i--;
							}
						}
						this.$message({
							type: 'success',
							message: '删除成功!'
						});
					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消删除'
						});
					});
				}
			},
		//选择触发
		select(select,row) {
			var id = row.id;
			for(var i in this.tableData) {
				if(id == this.tableData[i].id) {
					this.tableData[i].checked = !this.tableData[i].checked;
					break;
				}
			}
		},
		//全部选择触发
		selectAll(select) {
			if(select.length > 0) {
				this.selectYes = true;
				for(var i in this.tableData) {
					this.tableData[i].checked = true;
				}
			} else {
				this.selectYes = false;
				for(var i in this.tableData) {
					this.tableData[i].checked = false;
				}
			}
		},
		changeFun(val){

		},
		//分页
		changeCurPage(curPage) {
			var index = curPage;
		}
	}
}
</script>
<style scoped>
.search-bar,.fn-bar{overflow: hidden;padding: 10px;border: 1px solid #ebeef5;border-bottom: none;}
.search-bar span{display: block;line-height: 32px;height: 32px;float: left;}
.search-bar .input,.search-bar .button{height: 32px;float: left;}
.search-bar .input{width: 200px;margin: 0 10px 0 4px; }
.block{display: flex;justify-content: center;padding: 20px 0 0;}
</style>


