<template>
	<el-container style="padding:10px 0 10px 10px;background:#fff;flex:1;display:flex;">
		<!-- <el-button @click="storeClick">绑定store属性</el-button> -->
		
		<!-- <el-aside width="260px">
			<div class="current">菜单管理</div>
			<div class="menu-bar">
				<div class="li">
					<span @click="addClick" class="search"><i class="el-icon-plus"></i>增加</span>
				</div>
				<div class="li">
					<span @click="reviseClick" class="search"><i class="el-icon-edit-outline"></i>修改</span>
				</div>
				<div class="li">
					<span @click="deleteClick" class="search"><i class="el-icon-close"></i>删除</span>
				</div>
			</div>
			<el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
		</el-aside>
		<div class="content">
			<div class="current">功能模块管理</div>
			<el-main style="padding:0;">
				<div class="menu-bar">
					<div class="li" v-for="v in tableTopList">
						<span v-if="v.btn" class="search"><i :class="v.className"></i>{{ v.name }}</span>
						<em v-if="!v.btn">{{ v.name }}:<input class="input" size="small" type="text" name=""/></em>
					</div>
				</div>
			</el-main>
			<el-dialog width="400px"  title="添加菜单" :visible.sync="dialogTableVisible">
				<el-form :model="form">
					<el-form-item label="菜单名称：" :label-width="formLabelWidth">
						<el-input style="width:90%;" v-model="form.name" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="菜单类型：" :label-width="formLabelWidth">
						<el-select style="width:90%;" v-model="form.type" placeholder="请选择">
							<el-option v-for="item in options" :key="item.value" :label="item.label":value="item.value"></el-option>
						</el-select>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button size="small" @click="dialogTableVisible = false">取消</el-button>
					<el-button size="small" type="primary" @click="addConfirm">确定</el-button>
				</div>	
			</el-dialog>
		</div> -->
		<div id="map"></div>
	</el-container>
</template>

<script>

import store from '@/store'
export default {
	name: 'testF',
	data() {
		return {
			data: [{
				label: '功能菜单',
				children: [
				{
					label: '监控首页'
				},
				{
					label: '轨迹回放'
				},
				{
					label: '报表管理',
					children: [{
						label: '运行报表'
					},
					{
						label: '报警报表'
					},
					{
						label: '油量报表'
					},
					]
				},
				{
					label: '报警设置'
				},
				{
					label: '车辆管理'
				},
				{
					label: '新能源',
					children: [{
						label: '远程监控'
					},
					{
						label: '故障诊断'
					},
					{
						label: '能耗分析'
					},
					{
						label: '查询分析'
					}
					]
				},
				{
					label: '系统管理',
					children: [{
						label: '用户管理'
					},
					{
						label: '设备管理'
					},
					{
						label: '权限管理'
					}
					]
				}

				]
			}],
			defaultProps: {
				children: 'children',
				label: 'label'
			},
			tableTopList: [
			{
				btn: true,
				name: '增加',
				className:'el-icon-plus'
			},
			{
				btn: true,
				name: '修改',
				className:'el-icon-edit-outline'
			},
			{
				btn: true,
				name: '删除',
				className:"el-icon-close"
			},
			{
				btn: false,
				name: '模块唯一码',
				className: ''
			},
			{
				btn: false,
				name: '模块名称',
				className: ''
			},
			{
				btn: true,
				name: '查询',
				className: 'el-icon-search'
			}
			],
			menuTopList: [
			{
				btn: true,
				name: '增加',
				className:'el-icon-plus'
			},
			{
				btn: true,
				name: '修改',
				className:'el-icon-edit-outline'
			},
			{
				btn: true,
				name: '删除',
				className:"el-icon-close"
			}
			],
			dialogTableVisible: false,
			formLabelWidth: '110px',
			addId: '0',
			form:{
				name: '',
				type: '选项卡页'
			},
			options: [{
				value: '选项1',
				label: '单页面'
			}, {
				value: '选项2',
				label: '选项卡页'
			}]
		}
	},
	created(){

	},
	mounted(){
		var Oscript = document.getElementById('script');
		console.log(Oscript);
		if(Oscript == null || Oscript == undefined) {
			this.loadBMapScript();
		}
		window['bMapInit'] = () => {
	      // this.qeuryLocation();
	      var map = new BMap.Map('map');
	      var point = new BMap.Point(108.840053, 34.129522);
	      map.centerAndZoom(point, 10);
	      map.enableScrollWheelZoom();
		    var mapStyle={  style : "light" }//自定义地图模板
		    var myStyleJson=[  
		    {
		    	"featureType": "all",
		    	"elementType": "geometry",
		    	"stylers": {
		    		"hue": "#007fff",
		    		"saturation": 89
		    	}
		    },
		    {
		    	"featureType": "highway",
		    	"elementType": "all",
		    	"stylers": {
		    		"color": "#ffffff"
		    	}
		    },
		    {
		    	"featureType": "green",
		    	"elementType": "all",
		    	"stylers": {
		    		"color": "#ffffffff",
		    		"hue": "#000000",
		    		"weight": "1",
		    		"lightness": -29,
		    		"saturation": 1,
		    		"visibility": "on"
		    	}
		    }];
		    map.setMapStyle(mapStyle,{styleJson: myStyleJson });
		}  
	},
	methods: {
		handleNodeClick(data) {
			console.log(data);
			
		},
		loadBMapScript () {
			let script = document.createElement('script');
			script.id = 'script';
			script.src = 'http://api.map.baidu.com/api?v=2.0&ak=dF8sMa2zZUk8znVfxyvzxQjsiQWrDfW2&callback=bMapInit';
			document.body.appendChild(script);
			console.log(document.body)
		},
		storeClick() {
			this.$store.state.arr=[0,1,2,4,5];
		},
			//增加
			addClick(){
				this.dialogTableVisible = true;
			},
			//确认
			addConfirm(){
				if(this.form.name != '') {
					this.$message({
						message: '恭喜你，添加成功！',
						type: 'success'
					});
					this.dialogTableVisible = false;
				} else {
					this.$message.error('内容不能为空！');
				}
				
			},
			//修改
			reviseClick(){
				
			},
			//删除
			deleteClick(){
				
			}
		},
		store
	}
	</script>

	<style scoped>
	.content{flex: 1;padding: 0 20px;}
	.current{background: #cad5e1;height: 40px;line-height: 40px;font-size: 16px;color: #fff;padding-left: 15px;}
	.menu-bar{overflow: hidden;padding: 6px 0 0; border-right: none;}
	.menu-bar .li{float: left;}
	.menu-bar span{display: block;margin-left: 10px;width: 68px;height: 30px;line-height: 30px;font-size: 12px;color: #fff;background: #7489a4;border-radius: 4px;cursor: pointer;}
	.menu-bar em{margin: 0 0 0 10px;line-height: 30px;}
	.menu-bar span img{width: 17px;height: 14px;margin: 8px 5px 0;float: left;}
	.fold{position: absolute;top: 50%;left: 0;background: #e0e0e0;height: 50px;margin: -25px 0 0;cursor: pointer;width: 10px;}
	.menu-bar .search i{font-size: 16px;line-height: 30px;float: left;margin: 0 8px 0 5px;}
	.menu-bar .input{height: 26px;}
	#map{width:100%;}
	</style>


